<template>
    <div class="main-container">
        <ImageSpinner class="spinner"/>
        <h1>Art Institute of Chicago Image Viewer</h1>
        <label for="url-text">Image ID:</label>
        <input id="url-text" type="text" class="img-url-field" v-model="imgDataInput"/>
        <!-- <button @click="preview">Preview</button> -->
        <div class="img-container">
        <div class="img-div">
            <img :src="imgUrl" v-show="imgDataInput != ''" />
        </div>
    </div>
    </div>
</template>

<script>

import ImageSpinner from './ImageSpinner.vue';
    export default {
        components: {
            ImageSpinner
        },
        data() {
            return {
                imgDataInput: '',
                imgId: ''
            }
        },
        computed: {
            imgUrl() {
                return this.imgDataInput == '' ? '' : `https://www.artic.edu/iiif/2/${this.imgDataInput}/full/843,/0/default.jpg`
            }
        },
        methods: {
            preview() {
                this.imgData = this.imgDataInput;
            }
        }
    }
</script>

